.menu-nav {
  position: relative;
  margin-bottom: 261px;
  padding: 0 284px;

  @media (max-width: $tablet-width) {
    margin: 0 0 37px 0;
    padding: 0;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 24px;
  }
}

.menu-nav__logo {
  position: absolute;
  top: 318px;

  @media (max-width: $tablet-width) {
    top: 0;
    margin-top: 10px;
    margin-left: 22px;
    padding: 14px 0 6px;
  }

  @media (max-width: $mobile-width) {
    margin-left: 19px;
  }
}

.menu-nav__toggle {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
    position: relative;
    top: 0;
    right: 0;
    z-index: 2;
    width: 50px;
    min-height: 50px;
    margin-left: auto;
    background-color: $green;
    border: 0;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 28%;
      right: 18%;
      width: 23px;
      height: 1px;
      background-color: $basic-white;
      background-position: 50% 50%;
      box-shadow: 0 8px 0 0 $basic-white, 0 16px 0 0 $basic-white;
    }
  }
}

.menu-nav__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  margin: 0;
  padding: 0;
  list-style: none;
  border-bottom: 1px solid $gray-lighter;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.menu-nav__item:first-of-type {
  @media (max-width: $tablet-width) {
    margin-top: 50px;
  }
}

.menu-nav__list:last-child {
  @media (max-width: $tablet-width) {
    padding-bottom: 30%;
  }

  @media (max-width: $mobile-width) {
    padding-bottom: 100%;
  }
}

.menu-nav__item {
  margin-top: 10px;
  padding: 10px 20px;

  @media (max-width: $tablet-width) {
    position: relative;
    width: 100%;
    margin-top: 40px;
    margin-left: 0;
    padding: 17px 16px;
    background: $menu-background-gray;

    &::before {
      position: absolute;
      content: "";
      top: 38%;
      right: 5%;
      width: 18px;
      height: 18px;
      background-repeat: no-repeat;
      background-image: url("../img/icon-down-arrow.svg");
    }
  }

  @media (max-width: $mobile-width) {
    margin-top: 28px;
  }
}

.menu-nav__item a {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: $basic-white;
  opacity: 0.7;

  &:hover {
    opacity: 0.5;
  }

  &:focus,
  &:active {
    opacity: 1;
  }

  @media (max-width: $tablet-width) {
    font-size: 20px;
    line-height: 28px;
    color: $menu-gray;
  }
}

.menu-nav--opened .menu-nav__toggle {
  @media (max-width: $tablet-width) {
    background-color: $basic-white;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 46%;
      left: 27%;
      width: 25px;
      height: 2px;
      background-color: $gray;
      background-position: 50% 50%;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}

.menu-nav--opened .menu-nav__list {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: $basic-white;
}

.menu-nav--opened {
  background-color: $basic-white;
}

.menu-nav--opened .menu-nav__logo {
  z-index: -1;
}
